<template>
    <div>
        <div class="mt-5 d-flex justify-content-center" v-if="!products">
            <div class="spinner-grow text-dark m-5">
                <span class="sr-only">Loading...</span>
            </div>
            <div class="spinner-grow text-primary m-5">
                <span class="sr-only">Loading...</span>
            </div>
            <div class="spinner-grow text-dark m-5">
                <span class="sr-only">Loading...</span>
            </div>
        </div>

        <div class="d-flex align-items-stretch flex-wrap" v-if="products">
            <product-card v-for="product in products" :key="product.id" :product="product" />
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import ProductCard from './ProductCard'
export default {
    components:{
        ProductCard
    },
    computed: {
        ...mapState({
            products:  state => state.productModule.products
        })
    },
    methods: {
        ...mapActions('productModule',['getProducts'])
    },
    mounted() {
        this.getProducts()
    }
}
</script>

