<template>
    <div>
        <div class="mt-5 d-flex justify-content-center" v-if="!product">
            <div class="spinner-grow text-dark m-5">
                <span class="sr-only">Loading...</span>
            </div>
            <div class="spinner-grow text-primary m-5">
                <span class="sr-only">Loading...</span>
            </div>
            <div class="spinner-grow text-dark m-5">
                <span class="sr-only">Loading...</span>
            </div>
        </div>
        <div class="row mt-5" v-if="product">
            <div class="col-sm-4">
                <img src="https://via.placeholder.com/150x100" class="w-100">
            </div>
            <div class="col-sm-8 text-left">
                <h1>{{ product.title }}</h1>
                <h3>Rp{{ product.price }}</h3>
                <input type="text" v-model.number="quantity" class="text-center col-1 mr-2 p-1">
                <button class="btn btn-primary" @click="addToCart()">Add to Cart</button>
                <p class="mt-4">{{product.publisher}}</p>
                <div class="row">
                    <h5 class="col-sm-5">{{ product.author }}</h5>
                    <p class="col-sm-4"><i>{{ product.isbn }}</i></p>
                    <p class="col-sm-3">{{ product.year }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'
export default {
    props: ['id'],
    data() {
        return {
            quantity: 1
        }
    },

    methods: {
        ...mapActions('productModule',['getProduct']),
        ...mapActions('cartModule',['addProductToCart']),
        addToCart() {
            this.addProductToCart({
                product: this.product,
                quantity: this.quantity
            })
        }
    },

    computed: {
        ...mapState({
            product: state => state.productModule.product
        })
    },

    mounted() {
        this.getProduct(this.id)
    }
}
</script>

