<template>
    <div class="dropdown-menu p-2" style="min-width:320px;right:0;left:auto" aria-labelledby="triggerId">
        <div v-for="item in cart" :key="item.product.id">
            <div class="px-2 d-flex justify-content-between">
                <div>
                    <strong>{{ item.product.title }}</strong>
                    <br>
                    {{ item.quantity }} x Rp{{item.product.price}}
                </div>
                <div>
                    <a href="#" @click.prevent="removeProductFromCart(item.product)" class="badge badge-secondary">Remove</a>
                </div>
            </div>
        <hr>
        </div>
        <div class="d-flex justify-content-between">
            <span>Total: Rp{{ cartTotalPrice }}</span>
            <a href="#" @click.prevent="clearCartItems()">Clear Cart</a>
        </div>
    </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
export default {
    computed: {
        ...mapState({
            cart: state => state.cartModule.cart
        }),
        ...mapGetters('cartModule', ['cartTotalPrice'])
    },
    methods: {
        ...mapActions('cartModule',['removeProductFromCart', 'clearCartItems', 'getCartItems'])
        
    },
    mounted() {
        this.$store.dispatch('getCartItems');
    },
}
</script>

